/* Allmänna stilar */
:root {
  --primary-color: #34c234;
  --secondary-color: #2c3e50;
  --accent-color: #f39c12;
  --text-color: #333;
  --light-text-color: #fff;
  --background-color: #f4f7f9;
  --card-background: #fff;
  --box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  --border-radius: 8px;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.header {
  background-color: var(--primary-color);
  color: var(--light-text-color);
  padding: 1.5rem;
  text-align: center;
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  margin-bottom: 2rem;
  box-shadow: var(--box-shadow);
}

.header h1 {
  margin: 0;
  font-size: 2.5rem;
}

.main-content {
  padding: 2rem;
  background-color: var(--card-background);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.footer {
  text-align: center;
  margin-top: 2rem;
  padding: 1rem;
  color: var(--secondary-color);
}

/* Knappstilar */
.generic-button {
  background-color: var(--primary-color);
  color: var(--light-text-color);
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: var(--border-radius);
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  align-self: center;
  margin-top: 1rem;
}

.generic-button:hover {
  background-color: #2c9835;
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
  flex-wrap: wrap;
}

.back-button {
  background-color: transparent;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.back-button:hover {
  background-color: var(--primary-color);
  color: var(--light-text-color);
}

.save-pdf-button {
  margin-top: 1rem;
  width: 100%;
}

/* Formulärstilar */
.input-group {
  margin-bottom: 1.5rem;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.input-group input,
.input-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: var(--border-radius);
  font-size: 1rem;
  box-sizing: border-box;
}

/* Samma stil för alla input-fält */
.team-input-group input,
.schedule-time-inputs input,
.centered-input input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: var(--border-radius);
  font-size: 1rem;
  box-sizing: border-box;
}

/* Uppdaterad stil för centrerat fält */
.centered-input {
  max-width: 250px;
  margin: 0 auto 1.5rem;
  text-align: center;
}

.team-names-grid-container {
  margin-bottom: 1.5rem;
}

/* Centrera team-names-grid */
.team-names-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
}

.team-input-group {
  display: flex;
  flex-direction: column;
  flex: 1 1 200px;
  max-width: 250px;
}

.team-input-group label {
  font-weight: bold;
  margin-bottom: 0.25rem;
}

/* Resultatsektioner (Spelschema och Laguppdelning) */
.schedule-section {
  margin-top: 2rem;
}

.schedule-section h2 {
  text-align: center;
  margin-bottom: 1.5rem;
  color: var(--secondary-color);
  font-size: 2rem;
  border-bottom: 2px solid #eee;
  padding-bottom: 1rem;
}

.schedule-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  align-items: stretch;
}

.round-card {
  background-color: #f0f0f0;
  padding: 1.5rem;
  border-radius: var(--border-radius);
  flex-basis: 300px;
  min-height: 200px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  flex-grow: 1;
}

.round-card h3 {
  margin-top: 0;
  font-size: 1.5rem;
  color: var(--primary-color);
  border-bottom: 2px solid #ddd;
  padding-bottom: 0.5rem;
}

.match-list {
  list-style-type: none;
  padding: 0;
  margin-top: 1rem;
}

.match-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid #ddd;
}

.match-item:last-child {
  border-bottom: none;
}

.team-name {
  font-weight: bold;
}

.vs-text {
  font-style: italic;
  color: #888;
}

.match-time {
  font-size: 0.9rem;
  color: #555;
  margin-left: 0.5rem;
}

/* Laguppdelning */
.rosters-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

/* Felmeddelanden */
.error-message {
  text-align: center;
  color: #e74c3c;
  background-color: #fce8e8;
  padding: 1rem;
  border: 1px solid #e74c3c;
  border-radius: var(--border-radius);
  margin-top: 1.5rem;
}

/* Nya stilar för speltider */
.schedule-options {
  text-align: center;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.schedule-options label {
  display: inline-flex;
  align-items: center;
  font-weight: bold;
}

.schedule-options input[type="checkbox"] {
  margin-right: 0.5rem;
  cursor: pointer;
  transform: scale(1.2);
}

.schedule-time-inputs {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 1rem;
  flex-wrap: wrap;
}

.schedule-time-inputs .input-group {
  flex: 1;
  max-width: 250px;
}

/* Mobilanpassning */
@media (max-width: 768px) {
  .app-container {
    padding: 0.5rem;
  }

  .header h1 {
    font-size: 2rem;
  }

  .main-content {
    padding: 1rem;
  }

  .button-container {
    flex-direction: column;
    gap: 0.5rem;
  }

  .generic-button,
  .back-button {
    width: 100%;
    padding: 1rem;
  }

  .team-names-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
    display: grid;
    justify-content: center;
  }

  .schedule-container {
    flex-direction: column;
    gap: 1rem;
  }

  .round-card {
    flex-basis: auto;
    width: 100%;
    min-height: auto;
  }

  .input-group input,
  .input-group textarea {
    font-size: 0.9rem;
  }

  .schedule-time-inputs {
    flex-direction: column;
    gap: 1rem;
  }
}